

.cart-add-btn {
    background-color: rgba(0, 100, 42, 0.9);
    padding: 8px;
    border-radius: 5px;
    color: white;
    cursor: pointer;
}

.products-container {
    max-width: 1000px;
    margin: 0 auto;
    padding: 20px 30px;
    border-radius: 25px;
    background: linear-gradient(180deg, rgba(61, 255, 143, 0.85) 8.98%, #26AD5F 87.65%);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}